import { Client, Databases, ID } from 'appwrite';

// Configuration
const client = new Client()
    .setEndpoint(import.meta.env.VITE_APPWRITE_ENDPOINT)
    .setProject(import.meta.env.VITE_APPWRITE_PROJECT_ID);

const databases = new Databases(client);

// Constants
const DATABASE_ID = '68cfb8c900053dca6f90';
const COLLECTION_ID = 'vocabularies';

// Global variables
let isConnected = false;

// DOM elements
const statusDiv = document.getElementById('status');
const wordInput = document.getElementById('word');
const vietnameseInput = document.getElementById('vietnamese');
const examplesContainer = document.getElementById('examplesContainer');
const submitBtn = document.getElementById('submitBtn');
const vocabList = document.getElementById('vocabList');

// Utility functions
function log(message) {
    console.log(message);
}

function showStatus(message, type = 'info') {
    statusDiv.innerHTML = `<div class="status ${type}">${message}</div>`;
    statusDiv.style.display = 'block';
    
    if (type === 'success') {
        setTimeout(() => {
            statusDiv.style.display = 'none';
        }, 3000);
    }
}

// Appwrite functions
async function testConnection() {
    try {
        log(' Testing Appwrite connection...');
        showStatus(' ang kim tra kt ni...', 'loading');
        
        const response = await databases.listDocuments(DATABASE_ID, COLLECTION_ID, [], 1);
        log(' Connection test successful!');
        log(' Response:', response);
        
        isConnected = true;
        showStatus(' Kt ni Appwrite thnh cng!', 'success');
        return true;
    } catch (error) {
        log(' Connection test failed:', error);
        isConnected = false;
        
        if (error.code === 404) {
            showStatus(' Khng tm thy database/collection. Kim tra li ID!', 'error');
        } else if (error.code === 401) {
            showStatus(' Li xc thc. Kim tra Project ID!', 'error');
        } else {
            showStatus(' Li kt ni: ' + error.message, 'error');
        }
        return false;
    }
}

async function loadVocabularies() {
    if (!isConnected) {
        showStatus('Vui lng kt ni Appwrite trc', 'error');
        return;
    }

    try {
        log(' Loading vocabularies...');
        showStatus('ang ti danh sch t vng...', 'loading');
        
        const response = await databases.listDocuments(DATABASE_ID, COLLECTION_ID);
        log(' Documents fetched successfully');
        log(' Document count:', response.documents.length);
        
        if (response.documents.length === 0) {
            vocabList.innerHTML = '<p style="text-align: center; color: #666; padding: 20px;">Cha c t vng no</p>';
        } else {
            vocabList.innerHTML = response.documents.map(doc => {
                const data = doc.data;
                const sentences = data.sentences ? JSON.parse(data.sentences) : [];
                return `
                    <div class="vocab-item">
                        <div>
                            <div class="vocab-word">${data.word}</div>
                            <div class="vocab-examples">${sentences.length} cu v d</div>
                        </div>
                        <button class="delete-btn" onclick="deleteVocabulary('${doc.$id}')">Xa</button>
                    </div>
                `;
            }).join('');
        }
        
        showStatus(`  ti ${response.documents.length} t vng`, 'success');
    } catch (error) {
        log(' Error loading vocabularies:', error);
        showStatus(' Li ti danh sch: ' + error.message, 'error');
    }
}

async function saveVocabulary(word, sentences, vietnamese) {
    if (!isConnected) {
        showStatus('Vui lng kt ni Appwrite trc', 'error');
        return;
    }

    try {
        log(' Saving vocabulary to Appwrite...');
        showStatus('ang lu t vng...', 'loading');
        
        const data = {
            word: word,
            sentences: JSON.stringify(sentences),
            vietnamese: vietnamese || null,
            createdAt: Date.now().toString(),
            lastStudiedAt: '0',
            priorityScore: '0',
            totalAttempts: '0',
            correctAttempts: '0',
            memoryScore: '0.0'
        };

        log(' Data to send:', data);

        const response = await databases.createDocument(
            DATABASE_ID,
            COLLECTION_ID,
            ID.unique(),
            data
        );

        log(' Document created successfully:', response);
        showStatus('  lu t vng thnh cng!', 'success');
        
        // Reset form
        resetForm();
        
        // Reload vocabulary list
        loadVocabularies();
        
    } catch (error) {
        log(' Error saving vocabulary:', error);
        showStatus(' Li lu t vng: ' + error.message, 'error');
    }
}

async function deleteVocabulary(documentId) {
    if (!isConnected) {
        showStatus('Vui lng kt ni Appwrite trc', 'error');
        return;
    }

    if (!confirm('Bn c chc mun xa t vng ny?')) {
        return;
    }

    try {
        await databases.deleteDocument(DATABASE_ID, COLLECTION_ID, documentId);
        showStatus('  xa t vng thnh cng', 'success');
        loadVocabularies();
    } catch (error) {
        log(' Error deleting vocabulary:', error);
        showStatus(' Li xa t vng: ' + error.message, 'error');
    }
}

// UI functions
function addExample() {
    const container = examplesContainer;
    const exampleCount = container.children.length + 1;
    
    const exampleDiv = document.createElement('div');
    exampleDiv.className = 'example-item';
    exampleDiv.innerHTML = `
        <div class="example-header">
            <span class="example-number">V d ${exampleCount}</span>
            <button type="button" class="remove-example" onclick="removeExample(this)">Xa</button>
        </div>
        <textarea placeholder="Nhp cc cu ting Anh, mi cu mt dng&#10;V d:&#10;Hello, how are you?&#10;Hi, how are you doing?&#10;How are you today?" required></textarea>
    `;
    
    container.appendChild(exampleDiv);
    updateRemoveButtons();
}

function removeExample(button) {
    const container = examplesContainer;
    if (container.children.length > 1) {
        button.closest('.example-item').remove();
        updateExampleNumbers();
        updateRemoveButtons();
    }
}

function updateExampleNumbers() {
    const container = examplesContainer;
    const examples = container.querySelectorAll('.example-number');
    examples.forEach((span, index) => {
        span.textContent = `V d ${index + 1}`;
    });
}

function updateRemoveButtons() {
    const container = examplesContainer;
    const removeButtons = container.querySelectorAll('.remove-example');
    removeButtons.forEach((button, index) => {
        button.style.display = removeButtons.length > 1 ? 'block' : 'none';
    });
}

function resetForm() {
    document.getElementById('vocabularyForm').reset();
    examplesContainer.innerHTML = `
        <div class="example-item">
            <div class="example-header">
                <span class="example-number">V d 1</span>
                <button type="button" class="remove-example" onclick="removeExample(this)" style="display: none;">Xa</button>
            </div>
            <textarea placeholder="Nhp cc cu ting Anh, mi cu mt dng&#10;V d:&#10;Hello, how are you?&#10;Hi, how are you doing?&#10;How are you today?" required></textarea>
        </div>
    `;
}

// Event listeners
document.getElementById('testConnection').addEventListener('click', testConnection);
document.getElementById('loadVocabularies').addEventListener('click', loadVocabularies);
document.getElementById('addExample').addEventListener('click', addExample);

document.getElementById('vocabularyForm').addEventListener('submit', async function(e) {
    e.preventDefault();
    
    const word = wordInput.value.trim();
    const vietnamese = vietnameseInput.value.trim();
    
    if (!word) {
        showStatus('Vui lng nhp t vng', 'error');
        return;
    }

    // Collect all examples
    const exampleTextareas = document.querySelectorAll('#examplesContainer textarea');
    const allSentences = [];
    
    for (const textarea of exampleTextareas) {
        const sentences = textarea.value.trim().split('\n').filter(s => s.trim());
        allSentences.push(...sentences);
    }

    if (allSentences.length === 0) {
        showStatus('Vui lng nhp t nht mt cu v d', 'error');
        return;
    }

    await saveVocabulary(word, allSentences, vietnamese);
});

// Global functions for onclick handlers
window.removeExample = removeExample;
window.deleteVocabulary = deleteVocabulary;

// Initialize
document.addEventListener('DOMContentLoaded', function() {
    log(' App initialized');
    log(' Appwrite version:', '8.1.0');
    log(' Configuration:');
    log('  - Project ID:', import.meta.env.VITE_APPWRITE_PROJECT_ID);
    log('  - Endpoint:', import.meta.env.VITE_APPWRITE_ENDPOINT);
    log('  - Database ID:', DATABASE_ID);
    log('  - Collection ID:', COLLECTION_ID);
    
    // Auto-test connection
    setTimeout(testConnection, 1000);
});
